.wrapper {
  width: 100%;
  background-color: #F9F9F9;
  border: 1px solid #EEEDED;
  border-radius: 50vh;
  height: 50px;
  margin-bottom: 10px;
  display: flex;
  align-items: center;

  & input {
    color: #B8B8B8;
    font-weight: 500;
    width: 100%;
    margin: 0 30px;
    border: none;
    background-color: inherit;
    font-size: 14px;
    outline: none;

    &::placeholder {
      font-weight: 500;
      font-size: 14px;
      color: #B8B8B8;
    }
  }
}