.header {
  width: 100%;
  padding: 15px 20px;
  height: 242px;
  background-size: cover;
  background-image: url("menu.jpg");
  color: white;
  font-weight: 500;
  font-size: 40px;
  box-sizing: border-box;

  & div {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
  }
}

.menuItems {
  display: flex;
}
